.edit-page-main-imgs {
  min-height: 89vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 5% 0 5%;
}
.edit-page-img {
  margin: 10px;
  width: 50px;
}
.edit-page-container {
  padding: 10px 50px;
  text-align: center;
  h2 {
    font-size: 30px;
    font-family: roboSlab, serif;
  }
  .edit-page-form-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    input {
      width: 30%;
      min-width: 260px;
      height: 40px;
      margin: 10px auto;
      border-radius: 10px;
    }
    button {
      background-color: #3f507e;
      color: lightgoldenrodyellow;
      display: flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
      border: 2px black solid;
      border-radius: 10px;
      margin: 15px auto 30px auto;
      height: 40px;
      width: 150px;
      padding: 10px;
      font-size: 16px;
      font-weight: 700;
      transition: all 0.5s;
      &:hover {
        cursor: pointer;
        color: white;
        background-color: black;
      }
    }
    label {
      font-size: 20px;
    }
  }
}
